.header-grid-area {
  grid-area: head;
}

.card-box-grid-area {
  grid-area: card;
  font-family: 'Ubuntu', sans-serif;
  color: blanchedalmond;
}

.nav-grid-area {
  grid-area: navi;
}

.footer-grid-area {
  grid-area: foot;
}

.main-grid {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 60px 100px minmax(1200px, max-content) minmax(25px, max-content);
  grid-template-areas: "navi" "head" "card" "foot";
}

@media (min-width:901px) {

  .header-grid-area {
    grid-area: head;
  }

  .banner-left {
    grid-area: banner-left;
  }
  
  .banner-right {
    grid-area: banner-right;
  }
  
  .main-grid {
    display: grid;
    grid-template-columns: 1fr repeat(2, 450px) 1fr;
    grid-template-rows: 60px 120px minmax(1200px, max-content) minmax(25px, max-content);;
    grid-template-areas: "navi navi navi navi" "head head head head" "banner-left card card banner-right" "foot foot foot foot";
  }
}